import React from "react";
import "./index.css";
import Screen from "./Screen/Screen";
//import Button from "./Button/Button";
//import ButtonContainer from "./Button/ButtonContainer";
import { evaluate, exp } from "mathjs";

export default function App() {
  const buttons = [1, 2, 3, "+", 4, 5, 6, "-", 7, 8, 9, "*", "=", 0, "/", "%"];

  function handleClick(item) {
    if (item.toString() === "=") {
      //const newExpr = expr.toString() + item + evaluate(expr);
      setExpr(evaluate(expr));
      return;
    }
    const newExpr = expr.toString() + item;
    console.log(newExpr);
    setExpr(newExpr);
  }

  const [expr, setExpr] = React.useState("");
  // setExpr([newExpr]);

  return (
    <div className="App">
      <div className="screen">{expr}</div>
      <div className="btn-container">
        {buttons.map((el) => (
          <button key={el} className="btn" onClick={() => handleClick(el)}>
            {el}
          </button>
        ))}
      </div>
    </div>
  );
}
